import './App.css';
import { useState } from 'react';
import * as Util from './Util';

function App() {
  const [coinName, setCoinName] = useState('WAXP');
  const [startDate, setStartDate] = useState('2017-09-25');
  const [endDate, setEndDate] = useState(Util.getToday());
  // const [startDate, setStartDate] = useState('2020-09-25');
  // const [endDate, setEndDate] = useState('2020-10-20');
  const [investMoney, setInvestMoney] = useState('50000');
  const [buyRate, setBuyRateDate] = useState('-8');
  const [bitData, setBitData] = useState([]);
  const maxCount = 400;
  let totalCoinCount = 0;

  const handleSubmit = async (e) => {
    e.preventDefault();

    const diffDate = Util.dateDiff(endDate, startDate);
    let result = [];
    let date = '';

    for(let i=diffDate; i>0; i-= i > maxCount ? maxCount : i) {
      let count = i > maxCount ? maxCount : i;
      date = Util.addTime(startDate, 'day', '+' + (i - 1));

      let response = await fetch(`https://crix-api-cdn.upbit.com/v1/crix/candles/days?code=CRIX.UPBIT.KRW-${coinName}&count=${count}&to=${Util.formatDate(date)}T17:43:40Z`);
      let json;
      if (response.ok) {
        json = await response.json();
      } else {
        alert("HTTP-Error: " + response.status);
      }

      result.push(...json);
    }


    setBitData(result.reverse());
  }

  return (
    <div className="App">
      <div className="form-container">
        <form id="form-search" onSubmit={handleSubmit} >
          <div className="row-search" >
            <label className="label-search">
              코인 이름: 
              <input type="text" name="coin_name" value={coinName} onChange={e => setCoinName(e.target.value)}  />
            </label>
          </div>

          <div className="row-search" >
            <label className="label-search">
              시작 날짜: 
              <input type="text" name="start_date" value={startDate} onChange={e => setStartDate(e.target.value)}  />
            </label>

            <label className="label-search">
              종료 날짜: 
              <input type="text" name="end_date" value={endDate} onChange={e => setEndDate(e.target.value)}  />
            </label>
          </div>
          
          <div className="row-search">
            <label className="label-search">
              일별 투자금: 
              <input type="text" name="investMoney" value={investMoney} onChange={e => setInvestMoney(e.target.value)} />
            </label>

            <label className="label-search">
              매입가: 
              <input type="text" name="buy_rate" value={buyRate} onChange={e => setBuyRateDate(e.target.value)} /> %
            </label>
          </div>

          <input type="submit" value=" 출력 " />
        </form>
      </div>

      <div className="grid-container">
        <table className="bit-table">
          <thead>
            <tr>
              <th>날짜</th>
              <th>고가</th>
              <th>저가</th>
              <th>시작가</th>
              <th>마감가</th>
              <th>투자금</th>
              <th>구매 개수</th>
              <th>매매가</th>
              <th>총 코인 개수</th>
              <th>최종 금액</th>
              <th>수익률</th>
            </tr>
          </thead>
          <tbody>
            {bitData.length > 0 && bitData.map((data, index) => {
              let investedMoney = investMoney * (index + 1);
              let buyPrice = data.openingPrice * ((100 + Number(buyRate)) * 0.01);
              let coinCount = investedMoney / buyPrice;
              totalCoinCount += coinCount;
              let totalPrice = data.tradePrice * totalCoinCount;
              let rateOfReturn = totalPrice / investedMoney;

              return (
                <tr key={'tr_' + index}>
                  <td>{data.candleDateTime.substr(0,10)}</td>
                  <td>{Util.number_format(data.highPrice)}</td>
                  <td>{Util.number_format(data.lowPrice)}</td>
                  <td>{Util.number_format(data.openingPrice)}</td>
                  <td>{Util.number_format(data.tradePrice)}</td>
                  <td>{Util.number_format(investedMoney)}</td>
                  <td>{Util.number_format(coinCount)}</td>
                  <td>{Util.number_format(buyPrice)}</td>
                  <td>{Util.number_format(totalCoinCount)}</td>
                  <td>{Util.number_format(totalPrice)}</td>
                  <td>{Util.number_format(rateOfReturn)}</td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default App;
