export function getToday(){
    var date = new Date();
    var year = date.getFullYear();
    var month = ("0" + (1 + date.getMonth())).slice(-2);
    var day = ("0" + date.getDate()).slice(-2);

    return year + "-" + month + "-" + day;
}

export function formatDate(date){
    var year = date.getFullYear();
    var month = ("0" + (1 + date.getMonth())).slice(-2);
    var day = ("0" + date.getDate()).slice(-2);

    return year + "-" + month + "-" + day;
}

// 두개의 날짜를 비교하여 차이를 알려준다.
export function dateDiff(_date1, _date2) {
    var diffDate_1 = new Date(_date1);
    var diffDate_2 = new Date(_date2);
 
    diffDate_1 =new Date(diffDate_1.getFullYear(), diffDate_1.getMonth()+1, diffDate_1.getDate());
    diffDate_2 =new Date(diffDate_2.getFullYear(), diffDate_2.getMonth()+1, diffDate_2.getDate());
 
    var diff = Math.abs(diffDate_2.getTime() - diffDate_1.getTime());
    diff = Math.ceil(diff / (1000 * 3600 * 24));
 
    return diff;
}

export function addTime (date, paramType, param) {
  let unit = 0;
  if (paramType === 'day') {
    unit = 1000 * 60 * 60 * 24; // 일 단위
  } else if (paramType === 'hour') {
    unit = 1000 * 60 * 60; // 시간
  } else if (paramType === 'minute') {
    unit = 1000 * 60; // 분
  }
  return new Date(new Date(date).getTime() + unit * param);
};

export function number_format (number, round = 4){
    if(number==0) return 0;
    number = Math.round(Number(number) * Math.pow(10, round)) / Math.pow(10, round); 
        
    var reg = /(^[+-]?\d+)(\d{3})/;
    var n = (number + '');
    while (reg.test(n)) n = n.replace(reg, '$1' + ',' + '$2');

    return n;
};